
import { defineComponent } from "vue";

export default defineComponent({
  name: "base-header",
  computed: {
    IsLoggedIn() {
      return this.$store.getters.getIsLoggedIn;
    },
    nickname() {
      return this.$store.getters.getNickname;
    },
  },
  methods: {
    logoutHandler() {
      this.$store.commit("initData");
    },
    homeClickHandler() {
      this.$router.push("/");
    },
  },
});
